import React, { useEffect, useState } from 'react';
import { useSelector, useDispatch } from "react-redux";
import { setLocale } from "src/store/actions";
import { LogoPNG, LangArrowSVG } from 'src/assets';
import { LanguageType, RootState, langList, navList } from 'src/types';
import { refreshPage, useNavAnch } from 'src/utils';
import './index.less';

const Header: React.FC = () => {
    const [current, setCurrent] = useState<string>('home');
    const [anchKey] = useNavAnch();
    const [showLang, setShowLang] = useState<boolean>(false);
    const storeLang = useSelector<RootState>(state => state.localeState.locale);
    const dispatch = useDispatch();

    const switchLang = (lang: LanguageType) => {
        setShowLang(false);
        dispatch(setLocale(lang));
    }

    useEffect(() => {
        setCurrent(anchKey);
    }, [anchKey]);

    // useEffect(() => {
    //     // 监听页面导航是否进入页面可是区域
    //     const anchKes = Object.keys(navList);
    //     const intersectionoObserver = new IntersectionObserver(changes => {

    //         for (let change of changes) {
    //             const target = change;
    //             const eleId = target.target.id;
    //             // console.log('eleId', eleId);

    //             // 目标元素从视口下方滑入
    //             if (target.isIntersecting && target.boundingClientRect.top > 0) {
    //                 setCurrent(eleId);
    //             }

    //             // 目标元素从视口下方滑出 高亮上一个导航栏模块
    //             if (!target.isIntersecting && target.boundingClientRect.top > 0) {
    //                 const index = anchKes.indexOf(eleId);
    //                 setCurrent(anchKes[index - 1])
    //             }
    //         }

    //         // console.log('intersectionoObserver', changes);
    //     }, {})

    //     anchKes.map(key => intersectionoObserver.observe(document.querySelector(`#${key}`)))

    //     return () => {
    //         intersectionoObserver.disconnect();
    //     }

    // }, [])

    return (
        <div className="header">
            <div className="container">
                <div className="top-nav">
                    <img src={LogoPNG} alt="" onClick={() => refreshPage()} />
                    <div className="top-nav-right">
                        <div className='top-nav-anch'>
                            {
                                Object.keys(navList).map(key =>
                                    <a
                                        href={`#${key}`}
                                        key={key}
                                        className={current === key ? 'anch-active' : ''}
                                        onClick={() => setCurrent(key)}
                                    >
                                        {navList[key]}
                                    </a>
                                )
                            }
                        </div>
                        <div
                            className='top-nav-lang'
                            onMouseEnter={() => setShowLang(true)}
                            onMouseLeave={() => setShowLang(false)}
                        >
                            <span>{langList[storeLang as string]}</span>
                            <img src={LangArrowSVG} alt="" />
                        </div>
                    </div>
                </div>
                {
                    showLang &&
                    <div
                        className="nav-lang"
                        onMouseEnter={() => setShowLang(true)}
                        onMouseLeave={() => setShowLang(false)}
                    >
                        {
                            Object.keys(langList).map(key =>
                                <div
                                    className={storeLang === key ? 'nav-lang-item nav-lang-item-active' : 'nav-lang-item'}
                                    key={key}
                                    onClick={() => switchLang(key as LanguageType)}
                                >
                                    {langList[key]}
                                </div>
                            )
                        }
                    </div>
                }
            </div>
        </div>
    )
}

export default Header;